		}
	}
}

.hover-zoom-in-custom {
	.card-img {
		transform: scale(1) translateZ(0);
		transition: all 1.5s cubic-bezier(.19, 1, .22, 1);
		overflow: hidden;

		img {
			transform: scale(1.13) translateZ(0);
			transition: all 1.5s cubic-bezier(.19, 1, .22, 1);

		}
